body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: #000;
  font-family: monospace;
}

#game-container {
  width: 100vw;
  height: 100vh;
}

#ui-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  box-sizing: border-box;
  color: white;
  text-shadow: 1px 1px 2px black;
}

#crosshair {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

#crosshair::before, #crosshair::after {
  content: '';
  position: absolute;
  background: rgba(255, 255, 255, 0.8);
}

#crosshair::before {
  top: 9px;
  left: 0;
  width: 20px;
  height: 2px;
}

#crosshair::after {
  top: 0;
  left: 9px;
  width: 2px;
  height: 20px;
}
